import React from "react";

import { loadProjectTypes, loadEvents } from "../api"

class ProjectFilters extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      searchTerm: "",
      projectTypes: [],
      events: [],
      selectedProjectTypes: [],
      selectedEvents: [],
      params: new URLSearchParams(window.location.hash.split("#")[1]),
    }
  }

  selectProjectType = (tid) => {
    let selectedProjectTypes = this.state.selectedProjectTypes
    let params = this.state.params

    if (this.state.selectedProjectTypes.includes(tid)) {
      selectedProjectTypes = selectedProjectTypes.filter(item => item !== tid)
      params.delete(tid)
    } else {
      selectedProjectTypes.push(tid)
      if (!params.toString().includes(tid)) params.append(tid, "tid")
    }
    window.location.hash = "?"+params.toString()
    this.setState({ params: new URLSearchParams(params), selectedProjectTypes }, async () => {
      this.props.updateProjectTypes(selectedProjectTypes)
    })
  }

  selectEvent = (nid) => {
    let selectedEvents = this.state.selectedEvents
    let params = this.state.params

    if (selectedEvents.includes(nid)) {
      selectedEvents = selectedEvents.filter(item => item !== nid)
      params.delete(nid)
    } else {
      selectedEvents.push(nid)
      if (!params.toString().includes(nid)) params.append(nid, "nid")
    }
    window.location.hash = "?"+params.toString()
    this.setState({ params: new URLSearchParams(params), selectedEvents }, async () => {
      this.props.updateEvents(selectedEvents)
    })
  }

  // on windows reload -> check if URLParams in url after #
  checkURL = () => {
    let filter = this.state.params
    if (filter.toString().length) {
      this.setState({ selectedProjectTypes: [], selectedEvents: [], searchTerm: "" }, async () => {
        this.props.updateProjectTypes([])
        this.props.updateEvents([])
        this.props.doSearch("")
      })
      filter.forEach((type, ele) => {
        if (type === "nid") {
          try {
            this.selectEvent(this.state.events.find(item => item.nid === ele).nid)
          } catch {
            // is no event
          }
        } else if (type === "tid") {
          try {
            this.selectProjectType(this.state.projectTypes.find(item => item.tid === ele).tid)
          } catch {
            // is no projectType
          }
        } else {
          if (ele === "search") this.setState({searchTerm: type})
          this.props.doSearch(this.state.searchTerm)
        }
      })
    }
  }

  onChange = (event) => {
    this.setState({ searchTerm: event.target.value })
  }

  doSearch = (e) => {
    e.preventDefault()
    let params = this.state.params
    params.delete("search")
    params.delete("institution")

    if (this.state.searchTerm) {
      this.props.doSearch(this.state.searchTerm)
      if (!params.toString().includes("search") && this.state.searchTerm !== "") params.append("search", this.state.searchTerm)
    }
    window.location.hash = "?"+params.toString()
    this.setState({ params: new URLSearchParams(params) })
  }

  doReset = (e) => {
    e.preventDefault()
    this.setState({ params: new URLSearchParams(), selectedProjectTypes: [], selectedEvents: [], searchTerm: "" }, async () => {
      this.props.updateProjectTypes([])
      this.props.updateEvents([])
      this.props.doSearch("")
    })
    window.location.hash = window.location.hash.split("#")[0]
  }

  async componentDidMount() {
    const projectTypes = await loadProjectTypes()
    const events = await loadEvents()

    this.setState({
      ...this.state,
      projectTypes: projectTypes,
      events: events,
      loading: false,
    }, async () => {
      this.checkURL()
    })
  }

  renderTag = (term, key) => {
    if (this.state.selectedProjectTypes.includes(term.tid)) {
      return (<li key={key}><div className="badge"><a className="bef-link bef-link--selected badge" onClick={() => this.selectProjectType(term.tid)}>{term.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a className="bef-link badge" onClick={() => this.selectProjectType(term.tid)}>{term.title}</a></div></li>)
    }
  }

  renderEvent = (event, key) => {
    if (this.state.selectedEvents.includes(event.nid)) {
      return (<li key={key}><div className="badge"><a className="bef-link bef-link--selected badge" onClick={() => this.selectEvent(event.nid)}>{event.title}</a></div></li>)
    } else {
      return (<li key={key}><div className="badge"><a className="bef-link badge" onClick={() => this.selectEvent(event.nid)}>{event.title}</a></div></li>)
    }
  }

  render() {
    if (this.state.loading) {
      return "...."
    }

    return (
      <div className="view-filters col-lg-3">
        <form className="views-exposed-form bef-exposed-form" >
          <div className="form--inline clearfix row">
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-project-type form-item-project-type">
                <label htmlFor="edit-project-type">Typ des Projekts</label>
                <div data-drupal-selector="edit-project-type" multiple="multiple" name="project_type" id="edit-project-type" size="9" className="form-select bef-links">
                  <ul>
                    {this.state.projectTypes.map((term, key) => {
                      return (this.renderTag(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-select form-type-select js-form-item-event form-item-event">
                <label htmlFor="edit-event">Event</label>
                <div data-drupal-selector="edit-event" multiple="multiple" name="event" id="edit-event" size="8" className="form-select bef-links">
                  <ul>
                    {this.state.events.map((term, key) => {
                      return (this.renderEvent(term, key))
                    })}
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="js-form-item form-item js-form-type-textfield form-type-textfield js-form-item-search form-item-search">
                <label htmlFor="edit-search">Volltextsuche</label>
                <input type="text" id="edit-search" name="search" value={this.state.searchTerm} size="30" maxLength="128" className="form-text" onChange={this.onChange}/>
              </div>
            </div>
            <div className="col-12">
              <div data-drupal-selector="edit-actions" className="form-actions js-form-wrapper form-wrapper" id="edit-actions">
                <input type="submit" value="Filter anwenden" className="button js-form-submit form-submit" onClick={this.doSearch}/>
                <input type="submit" value="Zurücksetzen" className="button js-form-submit form-submit" onClick={this.doReset}/>
              </div>
          </div>
        </div>
      </form>
    </div>
    );
  }
}

export default ProjectFilters;