import React from "react";


import ProjectFilters from "./components/ProjectFilters"
import Cards from "./components/Cards"
import Pagination from "./components/Pagination"


import { loadCards } from "./api"


class Projects extends React.Component {


  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      searchTerm: "",
      count: 0,
      pages: 0,
      currentPage: 0,
      limit: 24,
      cards: [],
      selectedProjectTypes:[],
      selectedEvents:[],
    }
  }

  handlePaginationClick = async (e, page) => {
    this.setState({
      ...this.state,
      currentPage: page
    }, async () => {
      await this.loadData()
    })
  }

  updateProjectTypes = async (data) => {
    this.setState({
      selectedProjectTypes: data,
      currentPage: 0,
    }, async () => {
      await this.loadData()
    })
  }

  updateEvents = async (data) => {
    this.setState({
      selectedEvents: data,
      currentPage: 0,
    }, async () => {
      await this.loadData()
    })
  }

  doSearch = async (q) => {
    this.setState({
      searchTerm: q,
      currentPage: 0,
    }, async () => {
      await this.loadData()
    })
  }

  loadData = async () => {
    const limit = this.state.limit
    const offset = this.state.currentPage * limit

    const projects = this.state.selectedProjectTypes.join(",")
    const events   = this.state.selectedEvents.join(",")

    const cards = await loadCards(offset, projects, events, this.state.searchTerm)

    this.setState({
      ...this.state,
      cards: cards.data,
      pages: Math.ceil(cards.count / limit),
      loading: false,
    }, async () => {

    })
  }

  async componentDidMount() {
    this.loadData()
  }

  render() {
    if (this.state.loading) {
      return "...."
    }

    return (
      <div className="view view-projects view-id-projects view-display-id-page_1 container">
        <div className="row">
          <ProjectFilters
            updateProjectTypes={this.updateProjectTypes}
            updateEvents={this.updateEvents}
            doSearch={this.doSearch}
          />
          <Cards cards={this.state.cards} />
          <Pagination
            total={this.state.pages}
            current={this.state.currentPage}
            onChange={this.handlePaginationClick}
            limit={this.state.limit}
          />
        </div>
      </div>
    )
  }
}

export default Projects;