function Cards(props) {
  if (!props.cards.length) {
    return <div className="col-lg-9">
            <div className="">
              Es sind keine den Filtereinstellungen entsprechenden Projekte vorhanden.
            </div>
          </div>
  }
  return (
    <div className="view-content col-lg-9">
     {props.cards.map((card, key)=>{
        return (<div className="views-row" key={key}>
          <article data-history-node-id="1846" className="node node--type-project node--view-mode-teaser card">
            <div className="card-img card-img-top">
              <div className="field field--awards">{card.winner_title}</div>
              <a href={card.url}>
                <div className="field field--name-field-attributed-image field--type-entity-reference-revisions field--label-hidden field__item">
                  <div className="paragraph paragraph--type--project-image paragraph--view-mode--teaser">
                    <div className="field field--name-field-inline-image field--type-image field--label-hidden field__item">
                      <img src={card.image_url} width="480" height="360" alt="" className="image-style-project-preview-cropped"/>
                    </div>
                    <div className="paragraph--type--project-image--attribution"></div>
                  </div>
                </div>
              </a>
            </div>
            <div className="card-body">
              <h2 className="card-title">
                <a href={card.url} rel="bookmark">
                  <span className="field field--name-title field--type-string field--label-hidden">{card.title}</span>
                </a>
              </h2>
              <div className="node__content">
                <div className="field field--name-field-short-description field--type-string field--label-hidden field__item">{card.description}</div>
                <div className="field field--name-field-tags field--type-entity-reference field--label-visually_hidden">
                  <div className="field__label visually-hidden">Projekttyp</div>
                  <div className="field__items">
                    {card.project_types.map((project_type, index) => {
                      return (<div key={index} className="field__item"><span>{project_type}</span></div>)
                    })}
                  </div>
                </div>
                <div className="field field--name-field-event field--type-entity-reference field--label-visually_hidden">
                  <div className="field__label visually-hidden">Event</div>
                  <div className="field__item">
                    <a href={card.event_url}>{card.event_title}</a>
                  </div>
                </div>
                </div>
              </div>
            </article>
          </div>
     )})}
      </div>
  )
}

export default Cards;